/**
 * Threejs initializations
 */
// Scene
const scene = new THREE.Scene();

// Camera: field of view, aspect (width of window), near, far
const camera = new THREE.PerspectiveCamera(
    75,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
);

// Renderer
const renderer = new THREE.WebGLRenderer({
    antialias: true,
    physicallyCorrectLights: true,
    alpha: true
});
// Extend scene to window width and height
renderer.setSize(window.innerWidth, window.innerHeight);
// Remember tanFOV and window height for camera resize
var tanFOV = Math.tan( ( ( Math.PI / 180 ) * camera.fov / 2 ) );
var windowHeight = window.innerHeight;
// Resize: Adjust the window and elements
window.addEventListener ('resize', () => {
    // Resize camera
    camera.aspect = window.innerWidth / window.innerHeight;
    // adjust the FOV
    camera.fov = ( 360 / Math.PI ) * Math.atan( tanFOV * ( window.innerHeight / windowHeight ) );
    // Update camera
    camera.updateProjectionMatrix();
    // camera.lookAt(scene.position);
    // Render scene
    renderer.setSize( window.innerWidth, window.innerHeight );
    renderer.render( scene, camera );
});
const main = document.getElementById('main');
const logoQuarryDraw = document.getElementById('logoQuarryDraw');
