// Listen to scroll
var previousYposition;
var alreadyDisplayed = false;
var cooldown = true;
window.addEventListener("scroll", async function() {
    var scrollPosition = window.scrollY;
    var scrollDown = previousYposition < scrollPosition
    console.log(scrollPosition);

    // header display
    if (alreadyDisplayed && scrollPosition > previousYposition && scrollPosition < 7000 && cooldown) { // go down: hide
        header.classList.remove("display");
        header.classList.add("hide");
        alreadyDisplayed = false;
        cooldown = false;
        await cooldownHeader();
    } else if (!alreadyDisplayed && scrollPosition < previousYposition && cooldown) { // go up: display
        header.classList.remove("hide");
        header.classList.add("display");
        alreadyDisplayed = true;
        cooldown = false;
        await cooldownHeader();
    } else if (!alreadyDisplayed && scrollPosition > 4500 && cooldown) { // links header display
        header.classList.remove("hide");
        header.classList.add("display");
        alreadyDisplayed = true;
        cooldown = false;
        await cooldownHeader();
    }

    async function cooldownHeader() {
        setTimeout(() => {
            cooldown = true;
        }, 1000);
    }

    // header position pointer
    // vision
    if (scrollPosition > 1000 && scrollPosition < 2500) {
        vision.classList.add('hoverGradient');
    } else  {
        vision.classList.remove('hoverGradient');
    }

    // networks
    if (scrollPosition > 2500 && scrollPosition < 3000) {
        networks.classList.add('hoverGradient');
    } else  {
        networks.classList.remove('hoverGradient');
    }

    // team
    if (scrollPosition > 3000 && scrollPosition < 4000) {
        team.classList.add('hoverGradient');
    } else  {
        team.classList.remove('hoverGradient');
    }

    // links, start little earlier than dApp button
    if (scrollPosition > 4000) {
        links.classList.add('hoverGradient');
    } else  {
        links.classList.remove('hoverGradient');
    }
    if (scrollPosition > 4500) {
        dAppButton.classList.add('dApp-buttonHover');

    } else {
        dAppButton.classList.remove('dApp-buttonHover');
    }

    scene.traverse(obj => {
        if (obj.name == "logoQD") {
            var tl = gsap.timeline({scrollTrigger: {trigger: homeSection, start: "top top", end: "bottom top"}});
            tl
            .to(obj.position,
                {
                    x: 12,
                    duration: 5
                }
            ).to(obj.rotation,
                {
                    y: 4,
                    duration: 5
                }, "-=5"
            );
        }
    });

    previousYposition = scrollPosition;
});
// logoQD click, manages also portrait header display and 
var open;
logoHeaderQD.addEventListener("click", () => {
    if (window.innerWidth <= 730) { // display portrait header
        if (open) { // header open -> close it
            header.classList.remove("openHeaderFullScreen");
            navSection.classList.remove("displayContent");
            buttonSection.classList.remove("displayContent");
            logoHeaderQD.classList.remove("logoHeaderPosition");
            
            window.scrollTo({
                top: 0,
                left: 0,
                behavior: 'smooth'
            });

            open = false;
        } else { // header closed -> open it
            header.classList.add("openHeaderFullScreen");
            navSection.classList.add("displayContent");
            buttonSection.classList.add("displayContent");
            logoHeaderQD.classList.add("logoHeaderPosition");

            open = true;
        }
    } else { // redirect to home
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
    }
});
