// GLTF loader function
function loadGLTFAsset(_URL, posX, posY, posZ, sca, castSha, receiveSha, rotY, name) {
    assetLoaderGLTF.load(_URL.href, (gltf) => {
        // Geometry and texture
        const mesh = gltf.scene;

        // Add model to the scene
        scene.add(mesh);

        // Initialize properties
        mesh.position.set(posX, posY, posZ);
        mesh.scale.set(sca, sca, sca);
        mesh.castShadow = castSha;
        mesh.receiveShadow = receiveSha;
        mesh.doubleSided = true;

        // Y rotation
        mesh.rotateY(rotY);

        // Name
        mesh.name = name;

        // Log model
        console.log(mesh);
    }, undefined, function(error) {
        console.log(error);r
    });
};
