// Randomness
function getRandomArbitrary(min, max) {
    return Math.random() * (max - min) + min;
};

/**
 * Animation loop
 */
// Scrolling function
var lastT = 0;
function onScroll() {
    // How far we are from the top of the webpage, negative value
    const t = document.body.getBoundingClientRect().top;
    // console.log(t);
    let scrollDown = lastT > t;

    // Update scroll
    lastT = t;
}
// Get scroll of the whole body element
document.body.onscroll = onScroll;
// Animation loop function
function animate(t) {
    // Loop
    requestAnimationFrame(animate);

    // Render scene
    composer.render();
};
// Activate loop animation
animate();